<template>
    <div class="shop-icon">
        <svg class="icon" aria-hidden="true">
            <!--  '#'  -->
            <use :xlink:href="handleIconText"></use>
            
        </svg>
    </div>
</template>

<script>
export default {
    props:['iconText'],
    computed: {
        // data
        handleIconText () {
            return `#${this.iconText}`
        }
    }
}
</script>

<style lang="less" scoped>
    .shop-icon {
        display: inline-block;
    }
</style>



