export default {
  /**
   * @description 
   * @author wangkai
   * @param {String} k:
   * @returns  localStorage
   */
  getItem(k) {
    return JSON.parse(localStorage.getItem(k));
  },
  /**
   * @description 
   * @author wangkai
   * @param {String} k: 
   * @param {boolean || String} val:
   * @param {boolean} bool: booltruelocalStoragepush
   * boolfalselocalStorage
   */
  setItem(k, val, bool = false) {
    if (typeof (val) === 'object' && bool) {
      const arr = this.getItem(k) || [];
      arr.push(val);
      const string = JSON.stringify(arr);
      return localStorage.setItem(k, string);
    }
    const string = JSON.stringify(val);
    localStorage.setItem(k, string);
  }
}



