import Vue from 'vue';
import routes from './routes';
import VueRouter from 'vue-router';
// Vue
// 1. this.$routerthis.$route
// 2. <router-view></router-view>
Vue.use(VueRouter);

const router = new VueRouter({
    linkActiveClass: "active",
    routes,
})
export default router;


