<template>
    <div class="swiper">
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="item in toSwiperData.swiperList" :key="item.id" v-if="toSwiperData">
              <img :src="item.imgPath" alt="">
            </mt-swipe-item>
        </mt-swipe>
    </div>
</template>

<script>
export default {
  props: ["toSwiperData"],
  // 
  // updated () {
  //     console.log(this.toSwiperData);
  // }
};
</script>

<style lang="less">
// stylescoped,mint-ui swipe
.swiper {
  .mint-swipe-items-wrap {
    height: 5.3333rem;
  }
  img {
    width: 100%;
    height: 100%;
  }
}
</style>



